// 2. Router ê¸°ëŠ¥
function routeRender(routes) {
  // í•´ì‰¬ ê°’ ì—†ë‹¤ë©´ ì—ëŸ¬ë°œìƒ -> ì²˜ë¦¬ í•„ìš”
  if(!location.hash) {
    // history ì— ê¸°ë¡ ë‚¨ê¸°ì§€ ì•Šê³  í˜ì´ì§€ ì´ë™( ë©”ì¸í˜ì´ì§€ í•´ì‰¬ê°’ ë¶™ì—¬ì£¼ê¸°)
    history.replaceState(null,'','/#/')
  }
  // ë‚´ìš©ì´ ì¶œë ¥ë  ìœ„ì¹˜ì¸ router-view íƒœê·¸ ì°¾ê¸°
  const routerView = document.querySelector('router-view')
  // ì£¼ì†Œ ë¶€ë¶„ì˜ hash ê°’ì„ ì–»ì–´ë‚¼ ìˆ˜ ìˆìŒ, ë‹¤ë§Œ ì¿¼ë¦¬ìŠ¤íŠ¸ë§ ì •ë³´ êµ¬ë³„ì´ í•„ìš”(#about?name=heropy)
  // split í†µí•´ hash ì™€ ì¿¼ë¦¬ìŠ¤íŠ¸ë§ ë¶„ë¦¬, 0ë²ˆì§¸ Index ê°€ hash, 1ë²ˆì§¸ê°€ ì¿¼ë¦¬ìŠ¤íŠ¸ë§
  // êµ¬ì¡°ë¶„í•´í• ë‹¹ - splitì€ ë°°ì—´ì„ ë¦¬í„´í•˜ê¸° ë•Œë¬¸ì— [] ë¡œ í•´ì•¼í•¨!, {}ì€ object(ê°ì²´)
  const [ hash, queryString = ''] = location.hash.split('?')
  // http://localhost:1234/#/?a=123&b=456&c=789
  // split = ['a=123', 'b=456'...]
  // reduce ë©”ì†Œë“œ,ë°°ì—´ ê°œìˆ˜ ë§Œí¼ ì½œë°± ì‚¬ìš© ê°€ëŠ¥,2ë²ˆì§¸ ì¸ìˆ˜ë¡œ ì´ˆê¸° ë°ì´í„° ì„¤ì • ê°€ëŠ¥
  const query = queryString
    .split('&')
    .reduce((acc, cur)=> {
    // 'a=123' ì„ = ê¸°ì¤€ìœ¼ë¡œ í• ë‹¹ -> cur = { key: a, value: 123 }
    const [key, value] = cur.split('=')
    acc[key] = value
    return acc
  }, {})
  // query ê°’ì´ history ê°ì²´ì˜ state ê°’ìœ¼ë¡œ í• ë‹¹ -> state: {a: '123', b: '456', c: '789'}
  history.replaceState(query,'')


  // ë„˜ì–´ì˜¤ëŠ” ë¼ìš°í„° ì •ë³´ì—ì„œ hash ê°’ ì¶”ì¶œ
  // ì •ê·œí‘œí˜„ì‹ ì´ìš© -> ë¦¬í„°ëŸ´ ë°©ì‹(/#\/about\/?$/.)ì€ ì¤‘ê°„ì— ë³€ìˆ˜ì‚¬ìš© ë¶ˆê°€í•˜ê¸°ì— ìƒì„±ì ë°©ì‹ ì‚¬ìš©
  const currentRoute = routes.find(route => new RegExp(`${route.path}/?$`).test(hash))
  // ê° ê°ì²´ì˜ ì‹¤ì œ ì¶œë ¥í•  ê²ƒì€ ì†ì— ìˆëŠ” component ì†ì„± ì•ˆì— ìˆìŒ
  // ê·¸ component ì†ì„±ì€ Home, About ì—ì„œ ê°€ì ¸ì˜¨ ê²ƒ 
  routerView.innerHTML = ``
  // ë¼ìš°í„° ë·°ì˜ ë‚´ìš©ì„ ì»´í¬ë„ŒíŠ¸ì˜ ì¸ìŠ¤í„´ìŠ¤ì˜ elì„ ë°€ì–´ë„£ê¸°
  routerView.append(new currentRoute.component().el)


  // í˜ì´ì§€ê°€ ë°”ë€”ë•Œ, ì²« ì ‘ê·¼ì‹œ í˜ì´ì§€ ìŠ¤í¬ë¡¤ ìµœìƒë‹¨ ê³ ì •
  window.scrollTo(0,0)
}


// ë¼ìš°í„° ì •ë³´ë¥¼ ë§¤ê°œë³€ìˆ˜ë¡œ ì‚¬ìš©, index.js ì—ì„œ ë‚´ë³´ë‚¸ ê·¸ ë¼ìš°í„° ì •ë³´ ê¸°ë°˜
// ë¼ìš°í„° ì •ë³´ë¥¼ ë°›ìœ¼ë©´ routeRender() í•¨ìˆ˜ë¥¼ ì‹¤í–‰
export function createRouter(routes){
  // í•¨ìˆ˜ ë°˜í™˜
  return function(){
    window.addEventListener('popstate', ()=> {
      routeRender(routes)
    })
    // popstateëŠ” ìµœì´ˆì‹¤í–‰ ì•ˆë˜ê¸°ì— ì„ ì–¸í•˜ì—¬ 1íšŒ ì‹¤í–‰
    routeRender(routes)
  }
}


// 3. Store ê°œë…
export class Store {
  constructor(state){
    this.state = {}
    // subscribe ë©”ì†Œë“œ ì‹¤í–‰ìœ„í•´ observers ë¼ëŠ” ê°ì²´ ë°ì´í„° ì´ˆê¸°í™”
    this.observers = {}
    // object ë°˜ë³µì‹œì—ëŠ” for in, ë°–ì—ì„œ ë°›ì•„ì˜¤ëŠ” state(ê°ì²´ë°ì´í„°)ì˜ ì†ì„±ì„ for in ë¬¸ìœ¼ë¡œ ë°˜ë³µì²˜ë¦¬
    for (const key in state) {
      // ê°ì²´ë°ì´í„°ì˜ ì†ì„±ì„ ì •ì˜ -> (ì •ì˜ ëŒ€ìƒ, ì†ì„± ì´ë¦„, )
      Object.defineProperty(this.state, key,{
        // getter : key ê°’ ì‚¬ìš©ì‹œ ë™ì‘
        get: () => state[key], // state['message'] ,
        // setter : key ê°’ ì§€ì •(í• ë‹¹)ì‹œ ë™ì‘
        set: val => {
          // ìƒˆë¡œìš´ ë‚´ìš© ì§€ì •ì‹œ stateê°€ ê°€ì§„ ê°ì²´ë°ì´í„° ê°±ì‹ 
          state[key] = val
          // set í•¨ìˆ˜ ë™ì‘ì‹œ observersì— key ì— ë“±ë¡ëœ ì½œë°±í•¨ìˆ˜ë¥¼ ë™ì‘
          // this.observers['message']()
          // ì—¬ëŸ¬ê°œì˜ ì½œë°±ì„ ë‹´ì€ ë°°ì—´ì´ ë“¤ì–´ì˜¬í…Œë‹ˆ foreachë¥¼ ì‚¬ìš©í•˜ì—¬ ê·¸ ì½œë°±ì„ set ì—ì„œ ì‚¬ìš©í•  ìˆ˜ ìˆë„ë¡ ì„¤ì •
          this.observers[key].forEach(observer => observer(val));
        }
    })
    }
  }
  // state ë¶€ë¶„ì˜ ë°ì´í„°ê°€ ë³€ê²½ë˜ëŠ”ì§€ ì•„ë‹Œì§€ë¥¼ í™•ì¸(êµ¬ë…)
  // ì–´ë–¤ ë°ì´í„°ê°€ ë³€ê²½ë˜ì—ˆì„ë•Œ ì‹¤í–‰ë  ê¸°ëŠ¥ì„ ì •ì˜
  // key ì™€ callback í•¨ìˆ˜ë¥¼ ë°›ìŒ
  subscribe(key, cb){
    // key ì†ì„±ì— ì½œë°±í•¨ìˆ˜ ë“±ë¡, set í•¨ìˆ˜
    // this.observers['message'] = () => {}
    // { message(key): () => {} } => í•¨ìˆ˜ë¥¼ ì—¬ëŸ¬ê°œ ë“±ë¡í•  ìˆ˜ëŠ” ì—†ì„ê¹Œ?
    // í•˜ë‚˜ì˜ ì½œë°±í•¨ìˆ˜ê°€ ì•„ë‹Œ ì—¬ëŸ¬ê°œì˜ í•¨ìˆ˜ë¥¼ ë°°ì—´ì— ë°€ì–´ë„£ê¸°
    // ë°°ì—´ë°ì´í„°ì¸ì§€ ì•„ë‹Œì§€ë¥¼ ì¡°ì‚¬, ì•„ë‹ˆë¼ë©´ undefined ê¸°ì— push ê°€ ì•ˆë¨
    Array.isArray(this.observers[key])
    // ë°°ì—´ë°ì´í„°ë¼ë©´ ê¸°ì¡´ ê²ƒ ë’¤ì— í‘¸ì‹œ
    ? this.observers[key].push(cb)
    // ë°°ì—´ì˜ 0ë²ˆì§¸ ì•„ì´í…œìœ¼ë¡œ ì½œë°± í• ë‹¹
    : this.observers[key] = [cb]
  }
}